  private parseInputData(d){
    d.data.forEach(el => {
      this.savingChart.labels.push(el.date);
      this.savingChart.datasets[0].data.push(el.saving_percent);
      this.savingChart.datasets[1].data.push(el.saving_dollar);
      this.ecpmChart.labels.push(el.date);
      this.ecpmChart.datasets[0].data.push(el.ecpm_withTP);
      this.ecpmChart.datasets[1].data.push(el.ecpm_woTP);
      this.winRateChart.labels.push(el.date);
      this.winRateChart.datasets[0].data.push(el.winRate_withTP);
      this.winRateChart.datasets[1].data.push(el.winRate_woTP);
      
    });
  }



