import * as Chart from 'chart.js'
import { ViewChild, Component, ElementRef, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'app-chart',
    templateUrl: './chart.component.html',
    styleUrls: ['./chart.component.css']
})
export class ChartComponent implements OnInit {
    @ViewChild('chart') chart: ElementRef;



    @Input() public data: any[];
    @Input() public set: any;
    @Output() public chartClick: EventEmitter<any> = new EventEmitter();
    @Output() public chartHover: EventEmitter<any> = new EventEmitter();
 

    constructor() { }

    ngOnInit() {
        let ctx = this.chart.nativeElement.getContext('2d');
        this.set.data = this.data;
        const chart = new Chart(ctx, this.set);
    }

}



